body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.button {
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  font-size: 1.4em;
  background-color: #1d1d1d;
  color: #fff;
  flex: 1 1;
  outline: 1px solid #888;
  border: none; }

.operator {
  background-color: #44ff66;
  color: black;
  font-weight: bold; }

.display {
  height: 4em;
  display: flex;
  align-items: center;
  font-weight: bold;
  font-size: 1.4em;
  background-color: #000;
  color: #fff;
  padding: .5em;
  outline: 1px solid #888; }

.clear-btn {
  height: 4em;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.4em;
  flex: 1 1;
  outline: 1px solid #888;
  background-color: #44ff66;
  color: black;
  font-weight: bold;
  border: none; }

.App {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
}

.wrapper {
    width: 400px;
    height: 600px;
}

.row {
    display: flex;
    width: 100%;
}
